function vsTextDocument(opt) {
	if(!opt.eol || (opt.eol != '\n' && opt.eol != '\r\n')){
		opt.eol = '\n'
	}
	let docData = new ExtHostDocumentData(opt.uri, opt.text?opt.text.split(/\r\n|\r|\n/):[], opt.eol, opt.versionId, opt.languageId,
		opt.isClosed, opt.isDirty);
	return docData.document;
}
metatypes.registerObject("TextDocument", vsTextDocument);


