// export type DocumentSelector = DocumentFilter | string | ReadonlyArray<DocumentFilter | string>;
function match(selector /*DocumentSelector*/ , document /*TextDocument*/ ) {
	if (document == undefined) {
		return false;
	}
	let newdocument = document;
	// newdocument.text = '[show nothing in debug]';
	// console.log('match:'.mark(), "selector".mark('blue'), selector, 'document'.mark('blue'), newdocument)
	if (typeof selector === 'string') {
		if (document.languageId.toLowerCase() != selector.toLowerCase()) {
			// console.log('for string return false ', document.languageId, " != ", selector);
			return 0;
		}
	} else if (selector instanceof Object) {
		// console.log('for object')
		if (selector instanceof Array) {
			var ret = 0;
			for (s in selector) {
				if (match(selector[s], document) > 0) { // 其中一个通过则通过                    
					ret++;
				}
			}
			return ret;
		} else { // for DocumentFilter
			if (selector.hasOwnProperty('language')) {
				if (selector.language.toLowerCase() != document.languageId.toLowerCase()) {
					console.log('has language return false ', document.languageId, " != ", selector.language);
					return 0;
				}
			}
			if (selector.hasOwnProperty('scheme')) {
				if (document.uri.scheme.toLowerCase() != selector.scheme.toLowerCase()) {
					console.log('has scheme return false ', document.uri.scheme, " != ", selector.scheme);
					return 0;
				}
			}
			if (selector.hasOwnProperty('pattern')) {
				if (!minimatch(document.uri.fsPath, selector.pattern)) {
					console.log('has scheme return false ', document.uri.fsPath, " != ", selector.pattern);
					return 0;
				}
			}
			return 1;
		}
	}
	return 1;
}
async function NOTIMPL() {
	let callee = arguments.callee.name;
	return new Promise((resolve, reject) => {
		reject(`${callee} is valid but function is empty`)
	});
}

function dropCache(uid) {
	for (i in cache.items) {
		if(cache.items[i].token){
			cache.items[i].token.cancel();
		}
		delete cache.items[i]
	}
}

