const {	CompletionItemKind } = require("vs/workbench/api/common/extHostTypes");

const Type = {
	/**
	 * 默认是自由值域
	 */
	DEFALUT: "DEFALUT",
	/**
	 * 数字
	 */
	NUMBER: "NUMBER",
	/**
	 * 属性
	 */
	ATTRIBUTE: "ATTRIBUTE",
	/**
	 * 方法
	 */
	FUNCTION: "FUNCTION",
	ID: "ID",
	CLASS: "CLASS",
	SELECTOR: "SELECTOR",
	COLOR: "COLOR",
	/**
	 * 注释
	 */
	COMMENT: "COMMENT",
	/**
	 * 标签
	 */
	ELEMENT: "ELEMENT",
	/**
	 * 锚点
	 */
	ANCHOR: "ANCHOR",
	/**
	 * 文件
	 */
	FILE: "FILE",
	IMAGE: "IMAGE",
	/**
	 * 字体
	 */
	FONT: "FONT",
	/**
	 * 文件夹
	 */
	FOLDER: "FOLDER",
	/**
	 * 关键字
	 */
	KEYWORD: "KEYWORD",
	/**
	 * 代码块
	 */
	CODEBLOCK: "CODEBLOCK",
	/**
	 * 全提示列表
	 */
	OTHER: "OTHER",
	STRING: "STRING",
	ABC:"ABC",
	SNIPPET: "SNIPPET",
	/**
	 *事件 
	 */
	EVENT: "EVENT"
}

/**
 * 
 * @param {string} kind 
 */
function convert(kind) {
	let type = kind;
	if (typeof kind === 'string') {
		type = CompletionItemKind[kind];
	}
	switch (type) {
		// case :
		//   return Type.NUMBER;   // css 返回的 completioItem 中包含数字返回此类型
		case CompletionItemKind.Property:
		case CompletionItemKind.Variable:
			return Type.ATTRIBUTE;
		case CompletionItemKind.Method:
		case CompletionItemKind.Function:
		case CompletionItemKind.Constructor:
			return Type.FUNCTION;
			// case :
			//   return Type.ID;
			// case :
			//   return Type.CLASS;
			// case :
			//   return Type.SELECTOR;
		case CompletionItemKind.Color:
			return Type.COLOR;
			// case :
			//   return Type.COMMENT;
			// case :
			//   return Type.ELEMENT;  // html标签返回此类型
			// case :
			//   return Type.ANCHOR;   // @obsolete
		case CompletionItemKind.File:
			return Type.FILE;
			// case :
			//   return Type.IMAGE;  // <img src="" 提示返回此类型 扫描项目下所有图片
			// case :
			//   return Type.FONT;  // font 字体 返回此类型 直接返回 Type.Attribute 也行
		case CompletionItemKind.Folder:
			return Type.FOLDER;
		case CompletionItemKind.Keyword:
		case CompletionItemKind.Operator:
			return Type.KEYWORD;
			// case :
			//   return Type.CODEBLOCK; // @obsolete
			// case :
			//   return Type.OTHER; // @obsolete
		case CompletionItemKind.Text:
			return Type.ABC;
		case CompletionItemKind.Snippet:
			return Type.SNIPPET;
		case CompletionItemKind.Event:
			return Type.EVENT;
		default:
			return Type.DEFALUT;
	}
}


module.exports = convert;
